.nav__content__list {
  display: none;

  & li {
    padding: 20px;
    font-weight: bold;
    font-size: 24px;
    cursor: pointer;
    position: relative;
    overflow: hidden;

    &:hover {
      text-decoration: underline overline;
    }
  }

  @media all and (min-width: 1200px) {
    display: flex;
    width: 250px;
    height: 100vh;
    background-color: rgba(148, 147, 143, 0.22);
    border-right: 2px solid #ce2661;
    box-shadow: 0px 5px 10px 0px rgba(253, 253, 253, 0.315);
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
}

.nav__content__list--Mobile {
  width: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
  border-bottom: 1px solid rgb(190, 189, 189);
  box-shadow: 0px 5px 10px 0px rgba(206, 38, 97, 0.6);

  @media all and (min-width: 1200px) {
    display: none;
  }

  & li {
    padding: 10px;
  }
}

.content__icon {
  width: 50px;
  padding: 5px;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }
  @media all and(min-width:768px) {
    width: 60px;
    padding: 10px;
  }
}
